* {
  box-sizing: border-box;
}

body {
  background-color: #d7dae3;
}

form {
  height: 50px;
  background-color: #344ec2;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  display: flex;
}

input, button {
  height: 30px;
  border-color: #00f;
  border-radius: 7%;
}

button {
  cursor: pointer;
  background-color: #d7dae3;
  justify-content: center;
  align-items: center;
  display: flex;
}

button:hover, button:focus {
  background-color: #a5adc5;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.link {
  width: 40px;
  height: 35px;
  text-align: center;
  color: #f0f8ff;
  cursor: pointer;
  background-color: #7d90e4;
  border-color: #5b5c64;
  border-radius: 7%;
  text-decoration: none;
  display: block;
  position: absolute;
  top: 15px;
  left: 15px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .1);
}

.link:hover, .link:focus {
  background-color: #7080c9;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .2);
}

.load-more {
  color: #f0f8ff;
  background-color: #344ec2;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
}

.load-more:hover, .load-more:focus {
  background-color: #4860c8;
}

.gallery {
  flex-wrap: wrap;
  margin-bottom: -30px;
  margin-right: -30px;
  display: flex;
}

.photo-card {
  height: 350px;
  background-color: #f0f8ff;
  flex-direction: column;
  flex-basis: calc(33.3333% - 30px);
  margin-bottom: 30px;
  margin-right: 30px;
  display: flex;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.gallery__item {
  position: relative;
}

.img {
  object-fit: cover;
  width: 100%;
  height: 300px;
  display: block;
}

.info {
  height: 50px;
  justify-content: space-around;
  padding-top: 2px;
  padding-bottom: 2px;
  display: flex;
}

.info-item {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  display: flex;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.message {
  text-align: center;
  color: #344ec2;
}

#lightbox {
  z-index: 1111;
  width: 100%;
  height: 100%;
  background-color: rgba(157, 163, 210, .4);
  display: none;
  position: fixed;
  top: 0;
}

#lightbox.active {
  justify-content: center;
  align-items: center;
  display: flex;
}

#lightbox img {
  min-width: 600px;
  min-height: 400px;
  max-width: 85%;
  max-height: 85%;
  cursor: zoom-out;
  background-color: #344ec2;
  padding: 5px;
}

